import { Coordinate, ServiceType } from './types';

export interface MapService {
	accessToken: string;
	center: Coordinate;
	zoom: number;
	style: string;
}

export interface Layer {
	id: string;
	type: string;
	name: string;
	description: string;
	source: {
		type: string;
		data: GeoJSON.Feature<GeoJSON.Geometry> | GeoJSON.FeatureCollection<GeoJSON.Geometry>;
	};
	layout?: Record<string, unknown>;
	paint?: Record<string, unknown>;
}

export interface ContextMenuData {
	id: string,
	type: string,
	name: string;
	description?: string;
	businessObjectType?: string;
}

export interface SelectedContextData {
	selectedContext: ContextMenuData;
	sourceProperties: Record<string, unknown>;
}

export interface ConfigOptions {
	mapService?: MapService;
	layers?: Layer[];
	contextMenu?: ContextMenuData[];
}

export interface SemanticObjectParameter {
	key: string;
	value: string;
}

export default class Util {

	static constructMapService(config: any): MapService {
		return {
			accessToken: config.AccessToken ?? '',
			center: [config.DefaultCenterLongitude, config.DefaultCenterLatitude],
			zoom: config.DefaultZoomLevel,
			style: config.DefaultStyle
		};
	}

	static async constructLayer(layers: any[]): Promise<Layer[]> {
		const layerItems: Layer[] = [];
		let data: GeoJSON.Feature<GeoJSON.Geometry> | GeoJSON.FeatureCollection<GeoJSON.Geometry>;

		for (const layer of layers) {
			let url = layer.ServiceUrl;

			// TODO: will switch to getting destination from NSS config instead when it is ready
			if (!this.isPathAbsolute(url))
				url = `/nss/service/${url}`.replace(/\/\//, '/');

			try {
				const response = await fetch(url);

				if (!response.ok)
					throw new Error(response.statusText);

				if (layer.ServiceType?.Code === ServiceType.ODATA_V4 && layer.OdataFieldNameGeometry) {
					const responseData = await response.json();
					const features = responseData.value.map((value: any): GeoJSON.Feature<GeoJSON.Geometry> => {
						// TODO: properties should be populated when NSS is ready
						return {
							type: 'Feature',
							geometry: JSON.parse(value[layer.OdataFieldNameGeometry]),
							properties: {}
						};
					});

					data = {
						type: 'FeatureCollection',
						features
					};
				} else {
					data = await response.json();
				}

				layerItems.push({
					id: layer.ID,
					type: layer.LayerType?.Code,
					name: layer.Name,
					description: layer.Description,
					source: {
						type: ServiceType.GEOJSON,
						data
					},
					layout: layer.LayoutJson ? JSON.parse(layer.LayoutJson.replace(/'/g, '"')) : {},
					paint: layer.PaintJson ? JSON.parse(layer.PaintJson.replace(/'/g, '"')) : {},
				});
			} catch (error) {
				console.error(error);
			}
		}

		return layerItems;
	}

	static constructContextMenu(actions: any[]): ContextMenuData[] {
		const contextMenu = actions.map((action: any): ContextMenuData => {
			return {
				id: action.ID,
				type: action.ActionType?.Code,
				name: action.Name,
				description: action.Description,
				businessObjectType: action.BusinessObjectType?.name
			};
		});

		return contextMenu;
	}

	static handlebarReplacement(value: string, data: any): string {
		return value.replace(/\{\{([^}]+)\}\}/g, (match: string) => {
			return data[match.slice(2, -2)];
		});
	}


	static constructSemanticObjectParameters(parameters: SemanticObjectParameter[], data: any): Record<string, unknown> | undefined {
		if (!parameters || !data) return;
		if (Object.keys(parameters).length === 0 || Object.keys(data).length === 0) return;

		return parameters.reduce((obj: any, item: SemanticObjectParameter) => {
			const key = this.handlebarReplacement(item.key, data);
			const value = this.handlebarReplacement(item.value, data);

			obj[key] = value;
			return obj;
		}, {});
	}

	static isPathAbsolute(path: string): boolean {
		return /^https?:\/\//i.test(path);
	}

}