import '@luigi-project/core';
import { GeoJSONGeometry, stringify } from 'wellknown';
import EventEmitter from '../base/EventEmitter';
import MessageManager from './MessageManager';
import Util, { SelectedContextData, ConfigOptions, SemanticObjectParameter } from './Util';
import { EventType, ActionType } from './types';

export interface RendererOptions {
	path: string;
	label: string;
	url: string;
}

export interface ActionOptions {
	type: string;
	name: string;
	description: string;
	crossAppNavigation: {
		semanticObject: string;
		action: string;
		parameters: SemanticObjectParameter[];
	};
	url: string;
	businessObjectType: string;
}

export interface BusinesObject {
	id: string;
	type: string;
}

export default class Server {

	private _event = new EventEmitter();
	private _messageManager = MessageManager.getInstance();
	private _luigiCoreEl!: HTMLElement;
	private _isRendererInitialized = false;
	private _config!: ConfigOptions;
	private _actions: Record<string, ActionOptions> = {};
	private _businessObject!: BusinesObject;
	private _geoJsonFeatures: GeoJSON.Feature<GeoJSONGeometry>[] = [];

	constructor (selector: string) {
		this._luigiCoreEl = document.createElement('DIV');

		// TODO: clean up once Luigi Core Component is ready
		this._luigiCoreEl.style.width = 'var(--_nsc_container_width)';
		this._luigiCoreEl.style.height = 'var(--_nsc_container_height)';
		this._luigiCoreEl.style.position = 'var(--_nsc_container_position)';

		this._luigiCoreEl.setAttribute('luigi-app-root', '');

		setTimeout((): void => { document.querySelector(selector)?.appendChild(this._luigiCoreEl); }, 0);

		this._getConfig();
	}

	/**************************************************************************
	 * GETTERS AND SETTERS
	 **************************************************************************/

	set businessObject(value: BusinesObject) {
		this._businessObject = value;
	}

	/**************************************************************************
	 * EVENTS AND HANDLERS
	 **************************************************************************/

	onClientLoaded(listener: () => void): void {
		this._event.on(EventType.CLIENT_LOADED, listener);
	}

	onClientInitialized(listener: () => void): void {
		this._event.on(EventType.CLIENT_INITIALIZED, listener);
	}

	onSemanticObjectNavigation(listener: (semanticObject: string, action: string, params: any) => void): void {
		this._event.on(EventType.SEMANTIC_OBJECT_NAVIGATION, (event: any) => listener(event.semanticObject, event.action, event.params));
	}

	/**************************************************************************
	 * METHODS
	 **************************************************************************/

	navigate(nodePath: string): void {
		Luigi.navigation().navigate(nodePath);
	}

	fireCenter(): void {
		this._messageManager.sendCenterAction();
	}

	fireZoomIn(): void {
		this._messageManager.sendZoomInAction();
	}

	fireZoomOut(): void {
		this._messageManager.sendZoomOutAction();
	}

	fireLoadGeometries(geometries: GeoJSON.Geometry): void {
		this._messageManager.sendGeometries(geometries);
	}

	async saveGeometries(): Promise<void> {
		if (!this._businessObject || Object.keys(this._businessObject).length === 0 || this._geoJsonFeatures.length === 0) return;

		try {
			// TODO: for demo used, removed once authentication is ready
			const authResponse = await fetch('/nss/auth');

			if (!authResponse.ok)
				throw new Error(authResponse.statusText);

			const authorization = await authResponse.json();
			const response = await fetch('/nss/service/odata/v4/geoservice/BusinessObjects', {
				method: 'POST',
				headers: {
					'Content-Type': 'application/json',
					'Authorization': `Bearer ${authorization.access_token}`
				},
				body: JSON.stringify({
					appReferenceObjectId: this._businessObject.id,
					boType: this._businessObject.type,
					geometries: this._geoJsonFeatures.map((feature: GeoJSON.Feature<GeoJSONGeometry>): any => {
						return {
							boGeometry: stringify(feature.geometry)
						};
					})
				})
			});

			if (!response.ok)
				throw new Error(await response.json());

			this._messageManager.sendConfig(await this._getConfig());
		} catch (error) {
			console.error(error);
		}
	}

	private async _getConfig(): Promise<ConfigOptions | undefined> {
		const url = '/nss/config';

		try {
			const response = await fetch(url);

			if (!response.ok)
				throw new Error(response.statusText);

			const config = await response.json();

			// TODO: removed once scenario is ready
			let renderer: any = {};
			config?.Renderers.forEach((value: any): void => {
				if (value.ID === "770c602d-7630-4b06-969f-e94f835cd46a")
					renderer = value;
			});

			this._config = {
				mapService: Util.constructMapService(renderer),
				layers: await Util.constructLayer(config?.Layers),
				contextMenu: Util.constructContextMenu(config?.Actions)
			};

			config?.Actions.forEach((action: any): void => {
				this._actions[action.ID] = {
					type: action.ActionType?.Code,
					name: action.Name,
					description: action.Description,
					crossAppNavigation: {
						semanticObject: action.SemanticObject,
						action: action.Action,
						parameters: action.Parameters?.map((parameter: any): SemanticObjectParameter => {
							return {
								key: parameter.KeyName,
								value: parameter.Value
							};
						})
					},
					url: action.Url,
					businessObjectType: action.BusinessObjectType?.name
				};
			});

			// TODO: currently fixed to Mapbox renderer and will change once scenario is ready
			if (!this._isRendererInitialized) {
				setTimeout((): void => this._loadConfig({
					path: renderer?.ID,
					label: renderer?.Name,
					url: renderer?.Url
				}), 0);

				this._isRendererInitialized = true;
			}

			return this._config;
		} catch (error) {
			console.error(error);
		}
	}

	private _loadConfig(rendererOptions: RendererOptions): void {
		Luigi.unload();
		Luigi.setConfig({
			navigation: {
				nodes: () => [
					{
						pathSegment: rendererOptions.path,
						label: rendererOptions.label,
						hideFromNav: true,
						hideSideNav: true,
						viewUrl: rendererOptions.url,
						onNodeActivation: (node: any): void => {
							if (node)
								this._event.emit(EventType.CLIENT_LOADED);
						}
					}
				]
			},
			routing: {
				useHashRouting: false
			},
			settings: {
				hideNavigation: true,
				appLoadingIndicator: {
					hideAutomatically: true
				}
			},
			communication: {
				// TODO: standardize the namespace prefix for listeners
				customMessagesListeners: {
					'mfe-context-menu-item-selected': (event: any): void => {
						const selectedContext = event.data.selectedContext;
						const sourceProperties = event.data.sourceProperties;

						this._actionNavigate({
							selectedContext: {
								id: selectedContext.id,
								type: selectedContext.type,
								name: selectedContext.name,
								description: selectedContext.description,
								businessObjectType: selectedContext.businessObjectType
							},
							sourceProperties
						});
					},
					'mfe-client-initialized': (): void => {
						this._messageManager.sendConfig(this._config);
						this._event.emit(EventType.CLIENT_INITIALIZED);
					},
					'mfe-fetch-config': async (): Promise<void> => {
						this._messageManager.sendConfig(await this._getConfig());
					},
					'mfe-set-marker': (event: any): void => {
						this._geoJsonFeatures = event.data;
					}
				}
			}
		});
	}

	private _actionNavigate(contextData: SelectedContextData): void {
		if (!this._actions || !this._actions[contextData.selectedContext.id]) return;

		const action = this._actions[contextData.selectedContext.id];

		switch (action.type) {
			case ActionType.SEMATIC_OBJECT:
				this._event.emit(EventType.SEMANTIC_OBJECT_NAVIGATION, {
					semanticObject: action.crossAppNavigation.semanticObject,
					action: action.crossAppNavigation.action,
					params: Util.constructSemanticObjectParameters(action.crossAppNavigation.parameters, contextData.sourceProperties) || {}
				});
				break;
			case ActionType.URL:
				window.open(action.url, '_blank');
				break;
			default:
			// do nothing
		}
	}

}