import { EventListener } from './types';

export interface EventOptions {
	ClientLoaded?: ((event: Event) => void)[];
	ClientInitialized?: ((event: Event) => void)[];
	SemanticObjectNavigation?: ((event: Event) => void)[];
}

export default class EventEmitter {

	private events: Record<string, Array<EventListener>> = {};

	on(event: string, listener: EventListener): EventListener {
		if (typeof this.events[event] !== 'object') {
			this.events[event] = [];
		}

		this.events[event].push(listener);

		return () => this.removeListener(event, listener);
	}

	removeListener(event: string, listener: EventListener): void {
		if (typeof this.events[event] === 'object') {
			const index = this.events[event].indexOf(listener);
			if (index > -1) {
				this.events[event].splice(index, 1);
			}
		}
	}

	emit(event: string, ...args: any): void { // eslint-disable-line @typescript-eslint/no-explicit-any
		if (typeof this.events[event] === 'object') {
			this.events[event].forEach((listener: EventListener) => {
				listener.apply(this, args);
			});
		}
	}

	once(event: string, listener: EventListener): void {
		const remove = this.on(event, (...args: any) => {
			remove();
			listener.apply(this, args);
		});
	}

}