// TODO: Standardize the naming convention for message id
export default class MessageManager {

    private static instance: MessageManager;

    static getInstance(): MessageManager {
        if (!MessageManager.instance)
            MessageManager.instance = new MessageManager();

        return MessageManager.instance;
    }

    sendCenterAction(): void {
        this._sendCustomMessages({ id: 'mfe-map-center' });
    }

    sendZoomInAction(): void {
        this._sendCustomMessages({ id: 'mfe-map-zoom-in' });
    }

    sendZoomOutAction(): void {
        this._sendCustomMessages({ id: 'mfe-map-zoom-out' });
    }

    sendGeometries(geometries: any): void {
        this._sendCustomMessages({
            id: 'mfe-map-geometries',
            geometries: typeof geometries === 'string' ? JSON.parse(geometries) : geometries
        });
    }

    sendConfig(config: any): void {
        this._sendCustomMessages({
            id: 'mfe-configuration',
            ...config
        });
    }

    private _sendCustomMessages(data: any): void {
        Luigi.customMessages().send(Luigi.elements().getMicrofrontends()[0].id, {
            id: data.id,
            data
        });
    }

}